import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material';
import { WebsocketchatService } from 'projects/websocketchatlib/src/public-api';
import { LocalstorageService } from 'src/app/service/localstorage.service';
import {MatSnackBar} from '@angular/material';

@Component({
  selector: 'app-tabs',
  templateUrl: './tabs.component.html',
  styleUrls: ['./tabs.component.scss']
})
export class TabsComponent implements OnInit {

  constructor(
    private WebsocketService: WebsocketchatService,
    public dialog: MatDialog,
    private localstorage: LocalstorageService,
    private snackBar: MatSnackBar
  ) { }

  ngOnInit() {
  }

  reconnect() {
    this.WebsocketService.websocket = new WebSocket(this.WebsocketService.websocketUrl + "/" + this.localstorage.get("authToken"));
    this.WebsocketService.websocket.onopen = () => {
      this.WebsocketService.onopen();
    }
    this.WebsocketService.websocket.onmessage = (message: any) => {
      this.WebsocketService.onmsg(message);
      let msg: any = JSON.parse(message.data);
      if (msg && msg.fromId) {
        let msgList: Array<any> = new Array<any>();
        msgList = this.localstorage.getArr(msg.fromId);
        msgList.push(msg);
        this.localstorage.setArr(msg.fromId, msgList);
        this.localstorage.msgEvent.emit();
      }
    }

    this.WebsocketService.websocket.onclose = () => {
      this.WebsocketService.onclose();
    }
  }

  deleteall() {
    this.localstorage.removeAll();
    this.openSnackBar('缓存已清空', null);
  }

  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, null, {
      duration: 3000,
      verticalPosition: "top"
    });
  }

}
