import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { WebsocketchatlibModule } from 'websocketchatlib';
import { WebsocketchatService } from 'websocketchatlib';
import { AppComponent } from './app.component';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import { MatSidenavModule, MatTabsModule, MatSnackBarModule, MatDividerModule, MatToolbarModule, MatIconModule, MatButtonModule, MatCheckboxModule, MatInputModule, MatListModule, MatDialogModule, MatProgressSpinnerModule, MatProgressBarModule, MatMenuModule} from '@angular/material';
import { RouterModule, Routes } from '@angular/router';
import { LoginComponent } from './login/login.component';
import {MatCardModule} from '@angular/material/card';
import { FormsModule }   from '@angular/forms';
import { LoadingComponent } from './common/loading/loading.component';
import { ChatComponent } from './chat/chat.component';
import { SelectuserComponent } from './selectuser/selectuser.component';
import { LocalstorageService } from './service/localstorage.service';
import { TabsComponent } from './common/tabs/tabs.component';
import { TabComponent } from './tab/tab.component';
import { NgZorroAntdMobileModule } from 'ng-zorro-antd-mobile';
import { AnttabsComponent } from './common/anttabs/anttabs.component';
const appRoutes: Routes = [
  {
    path: 'login', component: LoginComponent
  },{
    path: 'chat', component: ChatComponent
  },{
    path: 'users', component: SelectuserComponent
  },{
    path: 'tabs', component: AnttabsComponent
  },{
    path: 'anttabs', component: TabsComponent
  },
];

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    LoadingComponent,
    ChatComponent,
    SelectuserComponent,
    TabsComponent,
    TabComponent,
    AnttabsComponent
  ],
  imports: [
    BrowserModule,
    WebsocketchatlibModule,
    BrowserAnimationsModule,
    FormsModule,
    MatButtonModule,
    MatCheckboxModule,
    MatInputModule,
    MatCardModule,
    MatListModule,
    MatDialogModule,
    MatProgressSpinnerModule,
    MatProgressBarModule,
    MatIconModule,
    MatToolbarModule,
    MatMenuModule,
    MatDividerModule,
    MatSnackBarModule,
    MatTabsModule,
    MatSidenavModule,
    NgZorroAntdMobileModule,
    RouterModule.forRoot(
      appRoutes,
      { enableTracing: false } // <-- debugging purposes only
    )
  ],
  providers: [WebsocketchatService, LocalstorageService],
  bootstrap: [AppComponent],
  entryComponents:[
    LoadingComponent
  ]
})
export class AppModule { }
