import { Component, OnInit } from '@angular/core';
import { WebsocketchatService } from 'websocketchatlib';
import { ActivatedRoute, Router } from '@angular/router';
import { LocalstorageService } from '../service/localstorage.service';
import {MatSnackBar} from '@angular/material';
@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.scss']
})
export class ChatComponent implements OnInit {

  msgList: Array<any> = new Array<any>();
  msg: string;
  toUser: string;
  constructor(
    private websocketService: WebsocketchatService,
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private localstorage: LocalstorageService,
    private snackBar: MatSnackBar
  ) {
  }

  ngOnInit() {
    this.toUser = this.activatedRoute.snapshot.params['id'];
    this.msgList = this.localstorage.getArr(this.localstorage.get('authToken') + this.toUser);
    this.localstorage.msgEvent.subscribe(() => {
      this.msgList = this.localstorage.getArr(this.localstorage.get('authToken') + this.toUser);
    })
  }

  onmsg(msg: any) {
    console.log("收到消息：");
    console.log(msg);
    this.msgList.push(JSON.parse(msg.data));
  }

  send() {
    this.websocketService.sendMsg(this.msg, this.websocketService.user.userId, this.toUser)
      .then((msg: any) => {
        if (!msg) {
          this.openSnackBar("用户未登录，无法接收信息", "Dance");
          return;
        }
        this.msgList.push(msg);
        this.localstorage.setArr(this.localstorage.get('authToken') + this.toUser, this.msgList);
        this.msg = null;
      }).catch((msg: any) => {
        this.openSnackBar("用户未登录，无法接收信息", "Dance");
      })
  }

  back() {
    this.router.navigate(['/tabs']);
  }

  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, null, {
      duration: 3000,
      verticalPosition: "top"
    });
  }

  delete() {
    this.localstorage.remove(this.localstorage.get('authToken') + this.toUser);
    this.msgList = new Array<any>();
    this.openSnackBar("聊天记录已被清空", "");
  }

}
